<template>
  <div>
    <b-card
      ref="cardAction"
      title="الطلبات الجديدة"
    >
      <b-row>
        <b-col cols="12">
          <b-table
            id="my-table"
            :per-page="perPage"
            :current-page="currentPage"
            head-variant
            hover
            responsive
            :items="items"
            :fields="fields"
            bordered
          >
            <template #head()="data">
              <span
                class="text-primary"
                style="font-size:18px"
              >{{ data.label }}</span>
            </template>

            <template #table-busy>
              <div class="text-center text-dark my-2">
                <b-spinner class="align-middle" />
                <strong> Loading... </strong>
              </div>
            </template>

            <template #cell(id)="row">
              {{ row.item.id }}
            </template>

            <template #cell(name)="row">
              {{ row.item.name }}
            </template>

            <template #cell(actions)="row">
              <b-button
                class="mr-1"
                size="sm"
                variant="primary"
                @click="edit(row.item)"
              > ارسال عرض سعر </b-button>
              <b-button
                class=""
                size="sm"
                variant="success"
                @click="confirmDelete(row.item.id)"
              > موافق على عرض السعر </b-button>
            </template>
          </b-table>
        </b-col>
      </b-row>
      <div class="mt-3">
        <b-pagination
          v-model="currentPage"
          :total-rows="rows"
          align="left"
          :per-page="perPage"
          aria-controls="my-table"
        />
      </div>
      <edit-offer :obj="Obj" />
    </b-card>
  </div>
</template>

<script>
import EditOffer from '@/views/factory/orders/EditOffer.vue'

import {
  BRow, BCol, BTable, BButton, BPagination, BCard,
} from 'bootstrap-vue'
import { faEye, faTrash, faPlus } from '@fortawesome/free-solid-svg-icons'
import { library } from '@fortawesome/fontawesome-svg-core'

library.add(faEye, faTrash, faPlus)

export default {
  name: 'NewOrderList',
  components: {
    EditOffer,
    BCard,
    BRow,
    BCol,
    BTable,
    BButton,
    BPagination,
  },
  data() {
    return {
      Obj: {},
      perPage: 6,
      currentPage: 1,
      add: true,
      busy: true,
      file: null,
      fields: [
        { key: 'id', label: 'م' },
        { key: 'name', label: 'اسم صاحب الطلب' },
        { key: 'discount', label: 'اسم المنتج' },
        { key: 'code', label: 'المبلغ المقترح' },
        { key: 'actions', label: 'العمليات' },
      ],
      items: [],
    }
  },
  computed: {
    rows() {
      return this.items.length
    },
  },
  created() {
    this.$store.dispatch('orders/fetchUser').then(res => {
      this.items = res.data.data
    })
  },
  methods: {
    edit(data) {
      this.Obj = data
      // console.log(this.Obj)
      this.$bvModal.show('EditOffer')
    },
    confirmDelete(id) {
      this.$swal({
        title: 'موافقة عرض السعر',
        text: 'هل أنت موافق على عرض السعر',
        icon: 'warning',
        showCancelButton: true,
        confirmButtonText: 'تاكيد عرض السعر',
        cancelButtonText: 'الغاء',
        customClass: {
          confirmButton: 'btn btn-primary',
          cancelButton: 'btn btn-outline-danger ml-1',
        },
        buttonsStyling: false,
      }).then(result => {
        if (result.value) {
          this.$swal({
            icon: 'success',
            title: 'عملية العرض',
            text: 'تم عرض السعر',
            customClass: {
              confirmButton: 'btn btn-success',
            },
          })
          this.$store.dispatch('orders/acceptOrder', id).then(() => {
          })
        }
      })
    },
  },
}
</script>

