<template>
  <b-modal
    id="EditOffer"
    cancel-variant="outline-secondary"
    ok-title="ارسال عرض السعر"
    cancel-title="الغاء"
    centered
    size="sm"
    title="ارسال عرض سعر"
    @ok="update"
  >
    <b-form>
      <b-form-group>
        <label for="name">عرض السعر :</label>
        <b-form-input
          id="name"
          v-model="price"
          type="text"
          placeholder="اكتب عرض السعر"
        />
        <label for="name">ملاحظات :</label>
        <b-form-input
          id="name"
          v-model="notes"
          type="text"
          placeholder="اكتب عرض السعر"
        />
      </b-form-group>
    </b-form>
  </b-modal>
</template>

<script>
import {
  BForm, BFormGroup, BFormInput, BModal,
} from 'bootstrap-vue'

export default {
  name: 'EditCoupon',
  components: {
    BForm,
    BFormGroup,
    BFormInput,
    BModal,
  },
  props: {
    obj: {
      type: Object,
      default: () => {
      },
    },
  },
  data() {
    return {
      order_id: '',
      dataName: '',
      dataDiscount: '',
      dataCode: '',
      notes: '',
      category_id: '',
      price: '',
    }
  },
  watch: {
    obj() {
      const {
        id, status, category_id, price, notes,
      } = JSON.parse(JSON.stringify(this.obj))
      this.order_id = id
      this.status = status
      // eslint-disable-next-line camelcase
      this.category_id = category_id
      this.price = price
      this.notes = notes
    },
  },
  methods: {
    update() {
      const obj = {
        order_id: this.order_id,
        status: this.status,
        category_id: this.category_id,
        price: this.price,
        notes: this.notes,
      }
      this.$store.dispatch('categories/offer', obj).then(() => {
      })
    },
  },
}
</script>

